
.logo {
    height: 7.5rem;
    margin: 1rem; }

.white-bg {
    background: #fff;
    color: #0A1128; }

.main-content {
    max-width: 100rem;
    margin: 2rem auto 0rem auto; }

.menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    border-bottom: 0.1rem solid #000;
    z-index: 1;
    height: 16%; }

.menu-content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0;

    li {
        display: flex;
        justify-content: space-around;
        align-items: center; } }

.menu h1 {
    font-size: 2.3rem;
    color: #0A1128; }

.menu h1 a {
    color: inherit; }

.menu ul {
    list-style: none;
    display: flex; }

.menu ul li a {
    display: block;
    padding: 2rem;
    font-size: 1.8rem;
    color: #0A1128;
    position: relative; }

.menu ul li a::after {
    content: '';
    position: absolute;
    bottom: 1rem;
    left: 25%;
    width: 0;
    height: 0.2rem;
    background: #FF0000;
    transition: all 300ms ease-in-out; }

.menu ul li a:hover::after {
    width: 50%;
    left: 25%; }


@media (max-width: 800px) {
    .menu {
        bottom: 0;
        text-align: center;
        display: none; }

    .menu-content, .menu-content ul {
        flex-direction: column;
        justify-content: space-around; }

    .menu-content {
        height: 100vh; }

    /*menu sanduiche*/

    .close-menu-label::after {
        content: '≡';
        position: fixed;
        z-index: 2;
        top: 1rem;
        right: 5rem;
        background: #0A1128;
        color: #fff;
        font-size: 5rem;
        line-height: 3rem;
        width: 5rem;
        height: 5rem;
        text-align: center;
        padding: .5rem;
        cursor: pointer; }

    /*x*/
    .close-menu:checked~.menu {
        display: block; }


    .close-menu:checked~.close-menu-label::after {
        display: block;
        content: '×'; }


    .menu-spacing {
        display: none; } }

