.pag {
    width: 100vw;
    margin: 0 auto;
    height: 100vh; }

.images {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    column-gap: 1rem;
    margin: 3rem 6rem;
    img {
        margin: 2rem auto;
        width: 20rem;
        height: 20rem;
        cursor: pointer;
        transition: all linear 0.4s;
        border-radius: 2rem; }
    img:hover {
        filter: grayscale(100%);
        transform: scale(1.1); } }

/*model*/

.model {
    width: 100%;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #000;
    transition: opacity .4s ease, visibility .4s ease, transform .5s ease-in-out;
    visibility: hidden;
    opacity: 0;
    transform: scale(0);
    overflow: hidden;
    z-index: 999; }

.model.open {
    visibility: visible;
    opacity: 1;
    transform: scale(1); }

.model {
    img {
        width: auto;
        max-width: 100%;
        height: auto;
        max-height: 100%;
        display: block;
        line-height: 0;
        box-sizing: border-box;
        padding: 20px 0 20px;
        margin: 0 auto; } }

.close {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 5rem;
    height: 5rem;
    padding: .5rem;
    cursor: pointer; }
